@import "src/styles/global.scss";
.banner {
  box-sizing: border-box;
  border: 1px solid $blackColor;
  border-radius: 20px;
  width: 100%;
  height: 300px;
  position: relative;
  z-index: 2;
  overflow: hidden;
  display: none;
  opacity: 0;
  transition: opacity 0.5s ease;

  &_active {
    display: block;
    opacity: 1;
  }

  &__image {
    object-position: center;
    object-fit: cover;
    width: 100%;
    height: 100%;
  }

  &__link {
    position: absolute;
    top: 0;
    left: 0;
    opacity: 0;
    transition: opacity 0.5s ease;
    width: 100%;
    height: 100%;

    &_active {
      opacity: 1;
    }
  }

  &__image {


  }
}
